.footer-container ul {
  display: flex;
  justify-content: space-around;
  width: 90%; }

.footer-container ul:nth-child(n+1) li {
  padding: 8px 15px; }

.footer-container ul:nth-child(n+1) li div {
  border: 1px solid transparent;
  transition: border 0.25s; }
  .footer-container ul:nth-child(n+1) li div:hover {
    border-bottom: 1px solid white !important;
    color: #bfbfbf !important; }

.footer-container ul:nth-child(n+1) li:hover {
  cursor: pointer; }

.footer-container ul:nth-child(n+2) li i {
  transition: transform 0.3s;
  font-size: 1.1rem;
  /*'small' is in the map of variables aka properties*/
  /*'small' is in the map of variables aka properties*/ }
  @media (min-width: 607px) {
    .footer-container ul:nth-child(n+2) li i {
      font-size: 1.25rem; } }
  @media (min-width: 992px) {
    .footer-container ul:nth-child(n+2) li i {
      font-size: 1.4rem; } }
  .footer-container ul:nth-child(n+2) li i:hover {
    transform: scale(1.5);
    cursor: pointer; }

.bottomBorder {
  border-top: 2px solid rgba(250, 250, 250, 0.4);
  width: 80%; }
